.worksContainer{
    /*border: 1px solid black;*/
    width: 100%;
    min-width: 280px;
    background-color:  #fff;
    /*height: 100vh;*/
    padding-bottom: 2rem;

}
@media screen and (max-width: 1200px){
    .worksItem{
        width: 100% !important;
        margin-bottom: 2rem;
    }
}
.worksContent{
    display: flex;
    justify-content: center;
    background-color:  #fff;
    /*border: 1px solid blue;*/
    margin-right: 15px;
    margin-left: 15px;
    flex-wrap: wrap;

}
.worksItem{
    background-color:  #f8f9fa;
    /*border: 1px solid red;*/
    width: calc(50% - 30px);
    flex-grow: 1;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    border-radius: 30px;
    margin-right: 15px;
    margin-left: 15px;
    padding: 30px 30px 24px 30px;
    text-decoration: none;
    color: #44566c;
}
.titleContent{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.img{
    width: 120px;
    height: 120px;

}